* {
  /* padding: 0; */
  margin: 0;
  text-decoration: none;
  list-style: none;
  /* box-sizing: border-box; */
  font-weight: 550;
}

header {
  background: rgba(217, 0, 0, 1);
  height: 65px;
  display: flex;
  z-index: 1000000;
  font-family: sans-serif;
  /* position: fixed; */
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
}
nav {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  width: 250px;
}
.headerUL {
  display: flex;
  flex-direction: row;
  /* float: right; */
}
nav ul li {
  margin: 0 5px;
}
.headerUL li a {
  color: white;
  font-size: 18px;
  text-transform: uppercase;
  padding: 7px 13px;
  border-radius: 2px;
  /* position: relative; */
}

.headerUL li a.active,
.headerUL li a:hover,
button:hover {
  background-color: rgb(255, 31, 31);
  transition: 0.5s;
}

.checkbtn {
  float: right;
  cursor: pointer;
  display: none;
}
#check {
  display: none;
}

.button {
  border-radius: 25px;
  display: none;
  background-color: rgb(173, 2, 2);
}

@media (max-width: 720px) {
  header {
    height: 160px;
  }
  nav {
    width: 80%;
  }
  .logo {
    width: 600px;
  }
  .checkbtn {
    display: block;
  }
  .headerUL {
    position: absolute;
    width: 100%;
    background: rgba(217, 0, 0, 0.98);

    top: 160px;
    left: -100%;
    text-align: center;
    transition: all 0.3s;
    border-top: 5px solid white;
    flex-direction: column;
  }
  .headerUL li {
    display: flex;
    width: 100%;
    position: relative;
    margin: auto;

    line-height: 100px;
  }
  .headerUL li a {
    font-size: 40px;
    margin: auto;
  }

  /* .checkbtn:hover{
    display: none;
   }
    */
  ul:active {
    left: 0;
    
  }
  .checkbtn:hover ~ ul{
    left: 0;

  }

}

