.slidesBody {
}
.slick-next:before {
  /* width: 46px; */
  margin: auto;
  height: 18px;
  border-radius: 20px;
  background-color: black;
  color: black;
  /* content: "" !important; */
  position: fixed;
  top: -10px;
  right: -20px;
  z-index: 1;
  
  /* background-image: url("../assets/images/next.png"); */
  background-size: cover; /* Resize the background image to cover the entire container */
}
.slick-prev:before {
  width: 46px;
  height: 46px;
  content: "" !important;
  position: fixed;
  top: -10px;
  left: -20px;
  z-index: 1;
  background-image: url(data:image/png;base64,);
  background-size: cover; /* Resize the background image to cover the entire container */
}
.slick-dots {
  background-color: transparent;
}
*:focus {
  outline: 0;
  outline: none;
}

